import React from 'react';
import { useState } from 'react';
import { RegisterDiv } from '../../styles/auth_css';
import firebase from '../../firebase';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

function Register() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [pw, setPw] = useState('');
  const [pwConfirm, setPwConfirm] = useState('');
  const [errorMsg, setErrorMsg] = useState('');
  const [nameCheck, setNameCheck] = useState(false);
  const [nameInfo, setNameInfo] = useState('');
  const [loading, setLoading] = useState(false);
  let navigate = useNavigate();

  //  
  const nameCheckFunc = (e) => {
    e.preventDefault();

    if (!name) {
      return alert('   .');
    }
    let body = {
      displayName: name,
    };

    axios
      .post('/api/user/nameCheck', body)
      .then((response) => {
        if (response.data.success) {
          if (response.data.check) {
            setNameCheck(true);
            setNameInfo('   .');
          } else {
            setNameInfo('   .');
          }
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  // 
  const signUpFunc = async (e) => {
    e.preventDefault();

    if (!(name && email && pw && pwConfirm)) {
      return alert('    .');
    }

    if ((pw && pwConfirm).length < 8) {
      return alert('  8   .');
    } else {
      if (pw !== pwConfirm) {
        return alert('     .');
      }
    }

    //   
    if (!nameCheck) {
      return alert('    .');
    }

    try {
      setLoading(true);
      const createUser = await firebase
        .auth()
        .createUserWithEmailAndPassword(email, pw);

      await createUser.user.updateProfile({
        displayName: name,
        photoURL:
          'https://kr.object.ncloudstorage.com/my-community/user/user.png',
      });

      let body = {
        displayName: createUser.user._delegate.displayName,
        email: createUser.user._delegate.email,
        uid: createUser.user._delegate.uid,
        photoURL:
          'https://kr.object.ncloudstorage.com/my-community/user/user.png',
      };

      axios
        .post('/api/user/register', body)
        .then((response) => {
          if (response.data.success) {
            setLoading(false);
            navigate('/login');
          } else {
            alert('  ');
          }
        })
        .catch((error) => {
          console.log(error);
        });
    } catch (error) {
      if (error.code === 'auth/email-already-in-use') {
        setErrorMsg('    .');
      }
    }
  };

  return (
    <RegisterDiv>
      <form className="registerForm">
        <h2>Sign up</h2>
        <input
          type="name"
          placeholder=""
          value={name}
          onChange={(e) => {
            setName(e.currentTarget.value);
          }}
        />
        {nameInfo && <p className="nameInfo">{nameInfo}</p>}

        <button
          disabled={nameCheck}
          className="nameCheckBtn"
          onClick={nameCheckFunc}
        >
           
        </button>
        <input
          type="email"
          placeholder=""
          value={email}
          onChange={(e) => {
            setEmail(e.currentTarget.value);
          }}
        />
        {errorMsg && <p className="errorMsg">{errorMsg}</p>}
        <input
          type="password"
          placeholder=""
          value={pw}
          onChange={(e) => {
            setPw(e.currentTarget.value);
          }}
        />
        <input
          type="password"
          placeholder=" "
          value={pwConfirm}
          onChange={(e) => {
            setPwConfirm(e.currentTarget.value);
          }}
        />
        <button disabled={loading} className="registerBtn" onClick={signUpFunc}>
          
        </button>
      </form>
    </RegisterDiv>
  );
}

export default Register;
