import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { useNavigate, useParams } from 'react-router-dom';
import { PostDetailDiv, Post, SpinnerDiv } from '../../styles/postDetail_css';
import { Spinner } from 'react-bootstrap';
import ReplyArea from '../reply/replyArea';
import moment from 'moment';
import 'moment/locale/ko';
import { useSelector } from 'react-redux';

function PostDetail() {
  const [postInfo, setPostInfo] = useState({});
  const [openEditModal, setOpenEditModal] = useState(false);
  const [loading, setLoading] = useState(false);
  const params = useParams();
  const modalRef = useRef();
  const user = useSelector((state) => state.user);
  let navigate = useNavigate();

  useOnClickOutside(modalRef, () => setOpenEditModal(false));

  //  &  time
  const setTime = (a, b) => {
    if (a !== b) {
      return moment(b).format('LLL') + ' ()';
    } else {
      return moment(a).format('LLL');
    }
  };

  //  
  const deletePost = (e) => {
    e.preventDefault();
    if (window.confirm(' ?')) {
      let body = {
        postNum: params.postNum,
      };

      axios
        .post('/api/post/delete', body)
        .then((response) => {
          if (response.data.success) {
            alert('  .');
            navigate('/');
          } else {
            alert('  .');
          }
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      return;
    }
  };

  // post 
  useEffect(() => {
    let body = {
      postNum: params.postNum,
    };

    axios
      .post('/api/post/detail', body)
      .then((response) => {
        if (response.data.success) {
          setPostInfo(response.data.post);
          setLoading(true);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }, [params]);

  return (
    <PostDetailDiv>
      {loading ? (
        <>
          <Post>
            <header className="header">
              <div className="postInfoDiv">
                <h2 className="title">{postInfo.title}</h2>

                <div className="postInfo">
                  <div className="avatar">
                    <img src={postInfo.author.photoURL} alt="avatar" />
                  </div>

                  <div className="record">
                    <strong className="name">
                      {postInfo.author.displayName}
                    </strong>

                    <p className="time">
                      {setTime(postInfo.createdAt, postInfo.updatedAt)}
                    </p>
                  </div>
                </div>
              </div>

              {/*      */}
              {user.uid === postInfo.author.uid && (
                <div className="editGroup">
                  <button
                    className="editBtn"
                    onClick={() => {
                      setOpenEditModal(true);
                    }}
                  >
                    <i className="fa-solid fa-ellipsis"></i>
                  </button>

                  {openEditModal && (
                    <div ref={modalRef} className="editModal">
                      <button
                        onClick={() => {
                          navigate(`/post/edit/${postInfo.postNum}`);
                        }}
                      >
                        
                      </button>

                      <button className="delete" onClick={deletePost}>
                        
                      </button>
                    </div>
                  )}
                </div>
              )}
            </header>

            <div className="content">
              {postInfo.image && (
                <div className="imgContainer">
                  <div className="imgDiv">
                    <img src={postInfo.image} alt="" />
                  </div>
                </div>
              )}

              <pre>{postInfo.content}</pre>
            </div>
          </Post>

          <ReplyArea postId={postInfo._id} />
        </>
      ) : (
        <SpinnerDiv>
          <Spinner animation="border" variant="info" />
        </SpinnerDiv>
      )}
    </PostDetailDiv>
  );
}

// useOnClickOutside Hook :     
function useOnClickOutside(ref, handler) {
  useEffect(() => {
    const listener = (event) => {
      if (!ref.current || ref.current.contains(event.target)) {
        return;
      }
      handler(event);
    };
    document.addEventListener('mousedown', listener);
    document.addEventListener('touchstart', listener);
    return () => {
      document.removeEventListener('mousedown', listener);
      document.removeEventListener('touchstart', listener);
    };
  }, [ref, handler]);
}

export default PostDetail;
