import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { MyPageDiv } from '../../styles/myPage_css';
import firebase from '../../firebase';

function MyPage() {
  const [currentImage, setCurrentImage] = useState('');
  const [currentName, setCurrentName] = useState('');
  const [editName, setEditName] = useState(false);
  const [nameCheck, setNameCheck] = useState(false);
  const [nameCheckInfo, setNameCheckInfo] = useState('');

  const user = useSelector((state) => state.user);
  let navigate = useNavigate();

  //  
  const nameCheckFunc = (e) => {
    e.preventDefault();

    if (!currentName) {
      return alert('   .');
    }

    let body = {
      displayName: currentName,
    };

    axios
      .post('/api/user/nameCheck', body)
      .then((response) => {
        if (response.data.success) {
          if (response.data.check) {
            setNameCheck(true);
            setNameCheckInfo('   .');
          } else {
            setNameCheckInfo('   .');
          }
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  //  
  const updateProfile = async (e) => {
    e.preventDefault();

    if (currentName === '' && !nameCheck) {
      return alert('    .');
    }

    try {
      await firebase.auth().currentUser.updateProfile({
        photoURL: currentImage,
        displayName: currentName,
      });
    } catch (error) {
      return alert('   .');
    }

    let body = {
      uid: user.uid,
      photoURL: currentImage,
      displayName: currentName,
    };

    axios
      .post('/api/user/profile/update', body)
      .then((response) => {
        if (response.data.success) {
          alert('  .');
          window.location.reload();
        } else {
          alert('   .');
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  //  
  const uploadImage = (e) => {
    const formData = new FormData();
    formData.append('file', e.target.files[0]);

    axios.post('/api/user/profile/image', formData).then((response) => {
      if (response.data.success) {
        setCurrentImage(response.data.filePath);
      }
    });
  };

  //   
  useEffect(() => {
    if (user.isLoading && !user.accessToken) {
      navigate('/login');
    } else {
      setCurrentImage(user.photoURL);
      setCurrentName(user.displayName);
    }
  }, [user, navigate]);

  return (
    <MyPageDiv>
      <div className="profileImgDiv">
        <label htmlFor="file">
          <input
            id="file"
            className="fileInput"
            type="file"
            accept="image/*"
            onChange={uploadImage}
          />

          <div className="image">
            <img src={currentImage} alt="profile" />
          </div>
        </label>
      </div>

      <div className="profileNameDiv">
        <p className="name">{currentName}</p>

        <button
          className="editBtn"
          onClick={(e) => {
            e.preventDefault();
            setEditName(true);
          }}
        >
          <i className="fa-solid fa-pen-to-square"></i>
        </button>
      </div>

      {editName && (
        <div className="editNameDiv">
          <label htmlFor="name" className="nameLabel">
            
          </label>

          <div className="editForm">
            <input
              id="name"
              className="input"
              type="text"
              value={currentName}
              onChange={(e) => {
                setCurrentName(e.currentTarget.value);
              }}
              placeholder=" ."
            />

            {nameCheckInfo && <p className="checkInfo">{nameCheckInfo}</p>}

            <button
              disabled={nameCheck}
              className="checkBtn"
              onClick={nameCheckFunc}
            >
               
            </button>

            <button className="saveBtn" onClick={updateProfile}>
              
            </button>
          </div>
        </div>
      )}
    </MyPageDiv>
  );
}

export default MyPage;
